<template>
    <div>
        <div class="row">
            <div class="col-12">
                <h3>    </h3>
                <div class="input-group my-4">
                    <input type="text" class="form-control" placeholder=" " v-model="dataFolder" readonly>
                    <div class="input-group-append">
                        <button class="btn btn-outline-primary" type="button" @click="showReadDialog"> </button>
                    </div>
                </div>
            </div>
        </div>    

        <div class="row text-right">
            <div class="col-12">
                <button class="btn btn-sm btn-outline-info" @click="startShare" :disabled="sharing == true"> </button>
                <button class="btn btn-sm btn-outline-danger" @click="stopShare" :disabled="sharing == false"> </button>
            </div>
        </div>
    </div>
</template>

<script>
    import {remote} from 'electron';
    export default {
        name: 'folderComponent',
        mounted(){
            
        },
        data () {
            return {
                dataFolder: '',
                sharing:false
            }
        },
        methods:{
            onChangeDir(e){
                //console.log(e.target.value);
            },
            showReadDialog(e){
                remote.dialog.showOpenDialog({ 
                    title:"  ",
                    properties: ["openDirectory"]
                }, (folderPaths) => {
                    if(folderPaths === undefined){
                        console.log("   .");
                        return;
                    }else{
                        this.dataFolder = folderPaths[0];
                    }
                });
            },
            startShare(e){
                // 
                if(this.dataFolder == ""){
                    this.$root.showToast("", "  ");
                    return;
                }
                let response = this.$root.ipc.sendSync("checkexist",this.dataFolder);
                if(response.result){
                    this.$root.showToast(" ", " .");
                    this.$root.setShare({share:true, folder:this.dataFolder});
                    this.sharing = true;
                }else {
                    this.$root.showToast(" ", response.msg);
                }
            },
            stopShare(e){ // 
                this.dataFolder = '';
                this.sharing = false;
                this.$root.setShare({share:false, folder:''});
            }
        }
    }
</script>
    
<style>
</style>



